import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {Constants} from '../../app.constants';
import {Grades} from '../../interfaces/interfaces';
import {GradeService} from '../../services/grade.service';

@Component({
  selector: 'app-grade',
  templateUrl: './grade.component.html',
  styleUrls: ['./grade.component.scss'],
  encapsulation: ViewEncapsulation.None,
  providers: [GradeService]
})
export class GradeComponent implements OnInit {
  public grades: Grades[] = [];
  public grade: Grades;

  // Documents: any;
  toggle = false;
  selectedAll: any;

  loading: boolean;
  errorMessage: any;
  public placeHolderMessage = '';

  constructor(public gradesService: GradeService,
              private route: ActivatedRoute,
              private router: Router) {
  }

  ngOnInit() {
    this.getGrade('');
  }

  public getGrade(search): void {
    this.loading = true;
    this.grades = [];
    this.gradesService.getGrades(search).subscribe((grades: any) => {
        this.grades = grades.grades || [];
        this.loading = false;
      }, (err) => {
        this.errorMessage = Constants.handleError(err, this.router);
        this.loading = false;
      }
    );
  }

  deleteGrade(id) {
    Constants.confirm({
      message: 'Do tou want to delete the selected grade?',
      confirmText: 'Delete'
    }, (willDelete) => {
      if (willDelete.value) {
        this.gradesService.deleteGrade(id).subscribe((status: boolean) => {
          if (status) {
            Constants.succeeded('Grade  has been deleted!', 'Successful');
            // todo: remove item, instead of reloading
            window.setTimeout(function () {
              location.reload();
            }, 2000)
          } else {
            Constants.error('Unable to delete the selected grade');
          }
        }, (err) => {
          Constants.handleError(err, this.router, true);
        });
      }
    });
  }
}
