import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {ActivatedRoute, Router} from '@angular/router';
import {Constants} from '../../../app.constants';
import {GradeService} from '../../../services/grade.service';
import {Grades} from '../../../interfaces/interfaces';

@Component({
  selector: 'app-edit-grade',
  templateUrl: './edit-grade.component.html',
  styleUrls: ['./edit-grade.component.scss'],
  encapsulation: ViewEncapsulation.None,
  providers: [GradeService]
})
export class EditGradeComponent implements OnInit {
  public gradeForm: FormGroup;
  grade: Grades = null;
  grades: Grades[] = [];
  errorMessage: any;
  loading: boolean;
  pageLoading: boolean;
  onSuccess: any;
  deleteMessageEnabled: boolean;

  constructor(private formBuilder: FormBuilder,
              private router: Router,
              private route: ActivatedRoute,
              private gradesService: GradeService) {
  }


  ngOnInit() {
    this.buildForm();
    const id = this.route.snapshot.params['id'];
    this.getGrade(id);

  }

  buildForm() {
    this.gradeForm = this.formBuilder.group({
      level: ['', Validators.compose([Validators.required, Validators.maxLength(255)])],
      description: ['', Validators.compose([Validators.required, Validators.maxLength(255)])],
    })
  }

  getGrade(id: number) {
    this.pageLoading = true;
    this.gradesService.getGradeByID(id)
      .subscribe((grade: Grades) => {
          this.grade = grade;
          this.gradeForm.patchValue({
            level: grade.level,
            description: grade.description
          });
          this.pageLoading = false;
        }, (err) => {
          this.errorMessage = Constants.handleError(err, this.router);
          this.pageLoading = false;
        }
      );
  }


  onSubmit({value, valid}: { value: Grades, valid: boolean }) {
    value.id = this.grade.id;
    this.loading = true;
    this.errorMessage = '';
    this.onSuccess = '';
    if (value.id) {
      this.gradesService.updateGrade(value)
        .subscribe((grade: Grades) => {
            if (grade) {
              this.grade = value;
              Constants.succeeded('Grade successfully updated');
            }
            this.loading = false;
          }, (err) => {
            Constants.handleError(err, this.router, true);
            this.loading = false;
          }
        )
    }
  }

  onDelete(event: Event) {
    event.preventDefault();

    Constants.confirm({
      message: 'Do you want to delete this grade?', confirmText: 'Delete'
    }, (willDelete) => {
      if (willDelete.value) {
        this.gradesService.deleteGrade(this.grade.id)
          .subscribe((status: boolean) => {
              if (status) {
                this.router.navigate(['/app/grades']);
              } else {
                Constants.error('Unable to delete grade');
              }
            },
            (err) => {
              Constants.handleError(err, this.router, true);
            }
          )
      }
    });
  }
}
