import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {ActivatedRoute, Router} from '@angular/router';
import {Constants} from '../../../app.constants';
import {GradeService} from '../../../services/grade.service';
import {Grades} from '../../../interfaces/interfaces';

@Component({
  selector: 'app-add-grade',
  templateUrl: './add-grade.component.html',
  styleUrls: ['./add-grade.component.scss'],
  encapsulation: ViewEncapsulation.None,
  providers: [GradeService]
})
export class AddGradeComponent implements OnInit {
  public grades: Grades[] = [];
  public grade: Grades;
  public selected = [];
  public NewGradeForm: FormGroup;
  errorMessage = '';
  loading: boolean;

  constructor(private formBuilder: FormBuilder,
              private router: Router,
              private route: ActivatedRoute,
              private gradesService: GradeService) {
  }

  ngOnInit() {
    this.buildForm();
  }

  buildForm() {

    this.NewGradeForm = this.formBuilder.group({
      level: ['', Validators.compose([Validators.required, Validators.maxLength(255)])],
      description: ['', Validators.compose([Validators.required])],
    })
    // this.addDetails();
  }

  onSubmit({value, valid}: { value: Grades, valid: boolean }) {
    this.loading = true;
    if (value) {
      // console.log(value)
      this.gradesService.addGrade(value)
        .subscribe((grades: Grades) => {
            if (grades) {
              this.NewGradeForm.reset();
              Constants.succeeded('Grade created successfully');
            }

            this.loading = false;
          },
          (err) => {
            Constants.handleError(err, this.router, true);
            this.loading = false;
          }
        )
    }

  }


}
