import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';
import {EditGradeComponent} from './edit-grade/edit-grade.component';
import {GradeComponent} from './grade.component';
import {AddGradeComponent} from './add-grade/add-grade.component';
import {PerfectScrollbarModule} from 'ngx-perfect-scrollbar';
import {RouterModule, Routes} from '@angular/router';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {CustomErrorModule} from '../../shared/custom-error/custom-error.module';
import {LoaderModule} from '../../shared/loader/loader.module';

export const routes: Routes = [
  {path: 'create', component: AddGradeComponent},
  {path: 'edit/:id', component: EditGradeComponent},
  {path: '', redirectTo: 'list', pathMatch: 'full'},
  {path: 'list', component: GradeComponent}
];


@NgModule({
  declarations: [AddGradeComponent, EditGradeComponent, GradeComponent],
  imports: [
    CommonModule,
    PerfectScrollbarModule,
    RouterModule.forChild(routes),
    FormsModule,
    ReactiveFormsModule,
    CustomErrorModule,
    LoaderModule,
  ]
})
export class GradeModule { }
